import React from 'react'
import { useHistory } from "react-router-dom"

import goIcon from "./assets/go.png"
import addPresetIcon from "./assets/ADD PRESET.png"
import editIcon from "./assets/EDIT.png"
import deletIcon from "./assets/DELETE.png"



function Presets() {

    const history = useHistory();

    const dummyData = [
        {
            queue: 0,
            name: "test_1",
            repeat: 1,
        },
        {
            queue: 0,
            name: "test_2",
            repeat: 1,
        },
        {
            queue: 0,
            name: "test_3",
            repeat: 1,
        },
        {
            queue: 0,
            name: "test_4",
            repeat: 1,
        },
    ]


    function onGoPressed() {
        history.push("/go")
    }

    function onPresetsPressed() {
        history.push("/presets")
    }

    function renderTableData() {
        return dummyData.map((preset) => {
            const { queue, name, repeat } = preset //destructuring
            return (
                <tr key={name}>
                    <td >{queue}</td>
                    <td style={{ width: "85%" }}>{name}</td>
                    <td >
                        <input type="text" value={repeat} />
                    </td>
                    <td >
                        <img className="table-button" src={editIcon} />
                    </td>
                    <td >
                        <img className="table-button" src={deletIcon} />
                    </td>
                </tr>
            )
        })
    }


    return (
        <div>
            <div className="nav">
                <img className="nav-button" src={goIcon} onClick={onGoPressed} />
                <img className="nav-button" src={addPresetIcon} onClick={onPresetsPressed} />
            </div>
            <table>
                <thead>
                    <tr>
                        <th style={{ width: "10%" }}>queue</th>
                        <th>name</th>
                        <th style={{ width: "10%" }}>repeat</th>
                        <th style={{ width: "10%" }}>edit</th>
                        <th style={{ width: "10%" }}>delete</th>
                    </tr>
                </thead>
                <tbody>
                    {renderTableData()}
                </tbody>
            </table>
        </div>
    )
}

export default Presets
