import React from 'react'
import { useHistory } from "react-router-dom"

import goIcon from "./assets/go.png"
import presetsIcon from "./assets/PRESETS.png"


function Splash() {

    const history = useHistory();


    function onGoPressed() {
        history.push("/go")
    }

    function onPresetsPressed() {
        history.push("/presets")
    }

    return (
        <div>
            <div className="nav">
                <img className="nav-button" src={goIcon} onClick={onGoPressed} />
                <img className="nav-button" src={presetsIcon} onClick={onPresetsPressed} />
            </div>
            <div className="logo">
                Want To React?
                <br />
                <span className="logo big">
                    12REACT
                </span>
                <br />
                <span className="logo small">
                    The Best U Can B
                </span>
            </div>
        </div>
    )
}

export default Splash
