import React from 'react'

import proIcon from "./assets/PRO.png"
import shareIcon from "./assets/SHARE.png"
import learnIcon from "./assets/LEARN.png"
import resetIcon from "./assets/RESET.png"
import kitIcon from "./assets/KIT.png"


function onGoPressed() {
    console.log("go pressed");
}

function Links() {
    return (
        <div className="footer-links">
            <img className="nav-button" src={proIcon} onClick={onGoPressed} />
            <img className="nav-button" src={shareIcon} onClick={onGoPressed} />
            <img className="nav-button" src={learnIcon} onClick={onGoPressed} />
            <img className="nav-button" src={resetIcon} onClick={onGoPressed} />
            <img className="nav-button" src={kitIcon} onClick={onGoPressed} />
        </div>
    )
}

export default Links
